<template>
  <div id="movie-manage">
    <!---->
    <div class="top">
      <el-col :span="12">
        <el-input placeholder="" v-model="input" class="input-with-select" style="width: 100%">
          <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
        </el-input>
      </el-col>
      <el-col :span="2" :offset="1">
        <el-button type="primary" size="small" @click="addMovie"></el-button>
      </el-col>
    </div>
    <!---->
    <div class="movie-table">
      <el-table border :data="tableData">
        <el-table-column label=" ID" align="center" width="70" prop="id"> </el-table-column>
        <el-table-column label="" width="90" show-overflow-tooltip prop="nm"> </el-table-column>
        <el-table-column label="" show-overflow-tooltip prop="enm"> </el-table-column>
        <el-table-column label="" align="center" show-overflow-tooltip prop="dir"> </el-table-column>
        <el-table-column label="" width="90" prop="version"> </el-table-column>
        <el-table-column label="" align="center" width="100" prop="cat"> </el-table-column>
        <el-table-column label="" width="110" prop="rt"> </el-table-column>
        <el-table-column label="" prop="wish"> </el-table-column>
        <el-table-column label="" prop="watched"> </el-table-column>
        <el-table-column label="" prop="showst" :formatter="formatShowst"> </el-table-column>
        <el-table-column width="200" align="center" label="">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!---->
    <div class="block" v-if="tableData.length">
      <el-pagination @current-change="currentChange" background layout="prev, pager, next" :page-size="8" :page-count="total"> </el-pagination>
    </div>
    <!---->
    <div>
      <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible" :modal-append-to-body="false" v-if="dialogFormVisible" :showClose="false">
        <el-form :label-position="labelPosition" :rules="rules" label-width="80px" :model="movieInfo">
          <el-form-item label="" prop="nm">
            <el-col :span="16">
              <el-input v-model="movieInfo.nm"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="enm">
            <el-col :span="16">
              <el-input v-model="movieInfo.enm"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="img">
            <el-col :span="16">
              <img :src="movieInfo.img" ref="previewImg" alt="" width="150px" height="200px" />
              <div style="position: relative;height: 64px">
                <el-button size="small" type="primary" style="position: absolute;left: 0;top: 0;cursor: pointer"></el-button>
                <input
                  type="file"
                  id="file"
                  name="file"
                  ref="uploadImg"
                  accept="image/png, image/jpeg, image/gif, image/jpg"
                  style="position: absolute;left: 0;top: 0;opacity: 0;height: 32px;width: 80px;cursor: pointer"
                  @change="changeImg"
                />
                <div slot="tip" class="el-upload__tip" style="position: absolute;left: 0;top: 32px;height: 32px">jpg/png500kb</div>
              </div>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="dir">
            <el-col :span="16">
              <el-input v-model="movieInfo.dir"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="star">
            <el-col :span="16">
              <el-input v-model="movieInfo.star"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="dur">
            <el-col :span="16">
              <el-input v-model="movieInfo.dur"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="language">
            <el-col :span="16">
              <el-select v-model="movieInfo.language" placeholder="" style="width: 100%">
                <el-option v-for="item in languageOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="src">
            <el-col :span="16">
              <el-select v-model="movieInfo.src" placeholder="" style="width: 100%">
                <el-option v-for="item in srcOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="cat">
            <el-col :span="16">
              <el-select v-model="movieInfo.cat" placeholder="" style="width: 100%">
                <el-option v-for="item in typeOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="version">
            <el-col :span="16">
              <el-select v-model="movieInfo.version" placeholder="" style="width: 100%">
                <el-option v-for="item in versionOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="rt">
            <el-col :span="16">
              <el-date-picker type="date" placeholder="" v-model="movieInfo.rt" style="width: 100%;"></el-date-picker>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="dra">
            <el-col :span="16">
              <el-input type="textarea" v-model="movieInfo.dra" :autosize="{ minRows: 8, maxRows: 10 }"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="showst">
            <el-col :span="16">
              <el-select v-model="movieInfo.showst" placeholder="" style="width: 100%">
                <el-option v-for="item in movieShowst" :key="item.value" :label="item.label" :value="item.value"> </el-option>
              </el-select>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="videoName">
            <el-col :span="16">
              <el-input v-model="movieInfo.videoName"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="videoImg">
            <el-col :span="16">
              <img :src="movieInfo.videoImg" ref="previewImg1" alt="" width="200px" height="120px" />
              <div style="position: relative;height: 20px">
                <el-button size="small" type="primary" style="position: absolute;left: 0;top: 0;cursor: pointer"></el-button>
                <input
                  type="file"
                  id="file"
                  name="file"
                  ref="uploadImg1"
                  accept="image/png, image/jpeg, image/gif, image/jpg"
                  style="position: absolute;left: 0;top: 0;opacity: 0;height: 32px;width: 80px;cursor: pointer"
                  @change="changeImg1"
                />
              </div>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="videoUrl">
            <el-col :span="16">
              <video :src="movieInfo.videoUrl" ref="previewVideo" controls :poster="movieInfo.videoImg" width="300px" height="150px"></video>
              <div style="position: relative;height: 40px">
                <el-button size="small" type="primary" style="position: absolute;left: 0;top: 0;cursor: pointer"></el-button>
                <input
                  type="file"
                  id="file"
                  name="file"
                  ref="uploadVideo"
                  accept="video/*"
                  style="position: absolute;left: 0;top: 0;opacity: 0;height: 32px;width: 80px;cursor: pointer"
                  @change="changeVideo"
                />
              </div>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="photos">
            <el-row :span="16">
              <ul class="upload-imgs">
                <div v-if="isNew">
                  <li v-for="(value, index) in newPhotos" :key="index">
                    <p class="img"><img :src="value" width="150px" height="90px" /><a class="close" @click="delImg2(index)"></a></p>
                  </li>
                </div>
                <div v-if="!isNew">
                  <li v-for="(value, index) in movieInfo.photosList" :key="index">
                    <p class="img"><img :src="value" width="150px" height="90px" /><a class="close" @click="delImg1(index)"></a></p>
                  </li>
                </div>
              </ul>
              <div style="position: relative;height: 20px">
                <el-button size="small" type="primary" style="position: absolute;left: 0;top: 0;cursor: pointer"></el-button>
                <input
                  type="file"
                  id="file"
                  name="file"
                  ref="uploadImgs"
                  accept="image/png, image/jpeg, image/gif, image/jpg"
                  style="position: absolute;left: 0;top: 0;opacity: 0;height: 32px;width: 80px;cursor: pointer"
                  @change="addImg"
                />
              </div>
            </el-row>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancel"> </el-button>
          <el-button type="primary" @click="manageMovieInfo()"> </el-button>
        </div>
      </el-dialog>
    </div>

    <!-- loading -->
    <div>
      <el-dialog title="..." :visible.sync="loadingDialogFormVisible" :modal-append-to-body="false" v-if="loadingDialogFormVisible" :show-close="false">
        <div class="sk-fading-circle">
          <div class="sk-circle sk-circle-1"></div>
          <div class="sk-circle sk-circle-2"></div>
          <div class="sk-circle sk-circle-3"></div>
          <div class="sk-circle sk-circle-4"></div>
          <div class="sk-circle sk-circle-5"></div>
          <div class="sk-circle sk-circle-6"></div>
          <div class="sk-circle sk-circle-7"></div>
          <div class="sk-circle sk-circle-8"></div>
          <div class="sk-circle sk-circle-9"></div>
          <div class="sk-circle sk-circle-10"></div>
          <div class="sk-circle sk-circle-11"></div>
          <div class="sk-circle sk-circle-12"></div>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { upLoadFile, getCurrentPageMovie, addMovieInfo, updateMovieInfo, deleteMovieInfo } from '../../../api/index'
import { MessageBox, Message } from 'element-ui'
import Vue from 'vue'
export default {
  name: 'MovieManage',
  data() {
    return {
      //
      server: 'http://localhost:8080',
      tableData: [],
      total: 0,
      currentPage: 1,
      movieInfo: {},
      newPhotos: [],
      isNew: false,
      dialogFormVisible: false,
      dialogTitle: '',
      labelPosition: 'right',
      input: '',
      searchInput: '',
      loadingDialogFormVisible: false,
      rules: {
        nm: [{ required: true, message: '', trigger: 'blur' }],
        img: [{ required: true, message: '', trigger: 'blur' }],
        dir: [{ required: true, message: '', trigger: 'blur' }],
        star: [{ required: true, message: '', trigger: 'blur' }],
        dur: [{ required: true, message: '', trigger: 'blur' }],
        language: [{ required: true, message: '', trigger: 'change' }],
        cat: [{ required: true, message: '', trigger: 'change' }],
        rt: [{ required: true, message: '', trigger: 'blur' }],
        dra: [{ required: true, message: '', trigger: 'blur' }]
      },
      movieShowst: [
        {
          value: 1,
          label: ''
        },
        {
          value: 2,
          label: ''
        },
        {
          value: 3,
          label: ''
        }
      ],
      srcOptions: [
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }
      ],
      languageOptions: [
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }
      ],
      versionOptions: [
        {
          value: 'v2d imax',
          label: '2Dimax'
        },
        {
          value: 'v3d imax',
          label: '3Dimax'
        }
      ],
      typeOptions: [
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }
      ],
      pickerPublicDate: {
        disabledDate(time) {
          return time.getTime() < Date.now()
        }
      }
    }
  },
  created() {
    this.loadCurrentPageMovie(this.currentPage, 8, '')
  },
  methods: {
    async loadCurrentPageMovie(pageNum, limit, input) {
      let json = await getCurrentPageMovie(pageNum, limit, input)
      if (json.state === 200) {
        this.tableData = json.data.beanList
        console.log(this.tableData)
        this.total = json.data.tr
      }
    },
    async currentChange(currentPage) {
      this.currentPage = currentPage
      this.loadCurrentPageMovie(this.currentPage, 8, this.searchInput)
    },
    formatShowst(row, column, cellValue) {
      var ret = '' //
      if (cellValue === 1) {
        ret = '' //
      } else if (cellValue == 2) {
        ret = ''
      } else {
        ret = ''
      }
      return ret
    },
    handleEdit(index, row) {
      this.dialogTitle = ''
      this.movieInfo = row
      this.isNew = false
      this.dialogFormVisible = true
      console.log(index, row)
    },
    async handleDelete(index, row) {
      console.log(index, row)
      MessageBox.confirm(', ', '').then(async value => {
        if (value === 'confirm') {
          let json = await deleteMovieInfo(row.id)
          if (json.state === 200) {
            this.loadCurrentPageMovie(this.currentPage, 8, this.searchInput)
            Message.success('')
          }
        }
      })
    },
    changeImg(e) {
      let reader = new FileReader()
      console.log(this.$refs.uploadImg.files)
      reader.readAsDataURL(this.$refs.uploadImg.files[0]) //
      let _this = this
      reader.onload = function() {
        //imgsrc
        _this.$refs.previewImg.src = this.result
        _this.movieInfo.img = this.result
      }
    },
    changeImg1(e) {
      let reader = new FileReader()
      console.log(this.$refs.uploadImg1.files)
      reader.readAsDataURL(this.$refs.uploadImg1.files[0]) //
      let _this = this
      reader.onload = function() {
        //imgsrc
        _this.$refs.previewImg1.src = this.result
        _this.movieInfo.videoImg = this.result
      }
    },
    changeVideo(e) {
      let reader = new FileReader()
      console.log(this.$refs.uploadVideo.files)
      reader.readAsDataURL(this.$refs.uploadVideo.files[0]) //
      let _this = this
      reader.onload = function() {
        //imgsrc
        _this.$refs.previewVideo.src = this.result
        _this.movieInfo.videoUrl = this.result
      }
    },
    async addImg(e) {
      let formData = new FormData()
      formData.append('img', this.$refs.uploadImgs.files[0])
      let json = await upLoadFile(formData)
      if (json.state == 200) {
        // this.movieInfo.photosList.push("file:///E:/file/"+json.data.img);
        if (this.dialogTitle === '') {
          this.movieInfo.photosList.push('https://liyuxiang.com.cn/' + json.data.img)
        } else {
          this.newPhotos.push('https://liyuxiang.com.cn/' + json.data.img)
        }
      }
      console.log(this.movieInfo)
      this.$refs.uploadImgs.files[0] == null
    },
    delImg1(index) {
      console.log('1')
      console.log(index)
      this.movieInfo.photosList.splice(index, 1)
    },
    delImg2(index) {
      console.log('2')
      console.log(index)
      this.movieInfo.newPhotos.splice(index, 1)
    },
    //
    async manageMovieInfo() {
      if (
        this.movieInfo.nm &&
        this.movieInfo.img &&
        this.movieInfo.dir &&
        this.movieInfo.star &&
        this.movieInfo.dur &&
        this.movieInfo.cat &&
        this.movieInfo.language &&
        this.movieInfo.rt &&
        this.movieInfo.dra
      ) {
        this.loadingDialogFormVisible = true
        //url
        let formData = new FormData()
        formData.append('img', this.$refs.uploadImg.files[0])
        formData.append('videoImg', this.$refs.uploadImg1.files[0])
        formData.append('video', this.$refs.uploadVideo.files[0])
        let json = await upLoadFile(formData)
        if (json.state === 200) {
          if (this.movieInfo.rt) {
            let date = new Date(this.movieInfo.rt)
            this.movieInfo.rt =
              date.getFullYear() + '-' + (date.getMonth() + 1 >= 10 ? date.getMonth() + 1 : '0' + (date.getMonth() + 1)) + '-' + (date.getDate() >= 10 ? date.getDate() : '0' + date.getDate())
          }
          if (json.data) {
            if (json.data.img) this.movieInfo.img = 'https://liyuxiang.com.cn/' + json.data.img
            if (json.data.videoImg) this.movieInfo.videoImg = 'https://liyuxiang.com.cn/' + json.data.videoImg
            if (json.data.video) this.movieInfo.videoUrl = json.data.video
          }
          //
          if (this.dialogTitle === '') {
            json = await updateMovieInfo(this.movieInfo)
            if (json.state === 200) {
              this.dialogFormVisible = false
              this.loadingDialogFormVisible = false
              Message.success('')
            } else {
              Message.error(json.message)
            }
          } else {
            this.movieInfo.photosList = this.newPhotos
            json = await updateMovieInfo(this.movieInfo)
            if (json.state === 200) {
              this.loadCurrentPageMovie(this.currentPage, 8, this.searchInput)
              this.dialogFormVisible = false
              this.loadingDialogFormVisible = false
              this.newPhotos = []
              Message.success('')
            } else {
              Message.error(json.message)
            }
          }
          this.dialogFormVisible = false
          this.loadingDialogFormVisible = false
          Message.success('')
        }
      } else {
        Message.error('')
      }
    },
    //
    search() {
      this.searchInput = this.input
      this.loadCurrentPageMovie(1, 8, this.searchInput)
    },
    //
    addMovie() {
      this.dialogTitle = ''
      this.movieInfo = {}
      this.isNew = true
      this.dialogFormVisible = true
    },
    cancel() {
      this.dialogFormVisible = false
      this.newPhotos = []
      this.loadCurrentPageMovie(this.currentPage, 8, this.searchInput)
    }
  }
}
</script>

<style>
.movie-table {
  width: 90%;
  min-width: 900px;
  margin: 0 auto;
  margin-bottom: 30px;
}
.top {
  display: flex;
  justify-content: center;
  align-items: center;
  /* padding: 30px 0; */
  margin-bottom: 30px;
}
.block {
  text-align: center;
}
.el-dialog__header {
  text-align: center;
}
.el-dialog__body .el-form-item {
  padding-left: 20%;
}
.upload-imgs {
  margin: 10px 0 30px 0;
  overflow: hidden;
  font-size: 0;
}
.upload-imgs li {
  position: relative;
  width: 150px;
  height: 90px;
  font-size: 14px;
  display: inline-block;
  margin-right: 20px;
  margin-top: 10px;
  text-align: center;
  vertical-align: middle;
}
.upload-imgs .img {
  position: relative;
  width: 94px;
  height: 94px;
  line-height: 94px;
}
.upload-imgs .img img {
  vertical-align: middle;
}
.upload-imgs .img .close {
  display: none;
}
.upload-imgs li:hover .img .close {
  display: block;
  position: absolute;
  right: -50px;
  top: 5px;
  line-height: 1;
  font-size: 22px;
  color: #fff;
}
.sk-fading-circle {
  width: 4em;
  height: 4em;
  position: relative;
  margin: auto;
}
.sk-fading-circle .sk-circle {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.sk-fading-circle .sk-circle:before {
  content: '';
  display: block;
  margin: 0 auto;
  width: 15%;
  height: 15%;
  background-color: #337ab7;
  border-radius: 100%;
  -webkit-animation: sk-fading-circle-delay 1.2s infinite ease-in-out both;
  animation: sk-fading-circle-delay 1.2s infinite ease-in-out both;
}
.sk-fading-circle .sk-circle-2 {
  -webkit-transform: rotate(30deg);
  transform: rotate(30deg);
}
.sk-fading-circle .sk-circle-3 {
  -webkit-transform: rotate(60deg);
  transform: rotate(60deg);
}
.sk-fading-circle .sk-circle-4 {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
.sk-fading-circle .sk-circle-5 {
  -webkit-transform: rotate(120deg);
  transform: rotate(120deg);
}
.sk-fading-circle .sk-circle-6 {
  -webkit-transform: rotate(150deg);
  transform: rotate(150deg);
}
.sk-fading-circle .sk-circle-7 {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}
.sk-fading-circle .sk-circle-8 {
  -webkit-transform: rotate(210deg);
  transform: rotate(210deg);
}
.sk-fading-circle .sk-circle-9 {
  -webkit-transform: rotate(240deg);
  transform: rotate(240deg);
}
.sk-fading-circle .sk-circle-10 {
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}
.sk-fading-circle .sk-circle-11 {
  -webkit-transform: rotate(300deg);
  transform: rotate(300deg);
}
.sk-fading-circle .sk-circle-12 {
  -webkit-transform: rotate(330deg);
  transform: rotate(330deg);
}
.sk-fading-circle .sk-circle-2:before {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}
.sk-fading-circle .sk-circle-3:before {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}
.sk-fading-circle .sk-circle-4:before {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}
.sk-fading-circle .sk-circle-5:before {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}
.sk-fading-circle .sk-circle-6:before {
  -webkit-animation-delay: -0.7s;
  animation-delay: -0.7s;
}
.sk-fading-circle .sk-circle-7:before {
  -webkit-animation-delay: -0.6s;
  animation-delay: -0.6s;
}
.sk-fading-circle .sk-circle-8:before {
  -webkit-animation-delay: -0.5s;
  animation-delay: -0.5s;
}
.sk-fading-circle .sk-circle-9:before {
  -webkit-animation-delay: -0.4s;
  animation-delay: -0.4s;
}
.sk-fading-circle .sk-circle-10:before {
  -webkit-animation-delay: -0.3s;
  animation-delay: -0.3s;
}
.sk-fading-circle .sk-circle-11:before {
  -webkit-animation-delay: -0.2s;
  animation-delay: -0.2s;
}
.sk-fading-circle .sk-circle-12:before {
  -webkit-animation-delay: -0.1s;
  animation-delay: -0.1s;
}

@-webkit-keyframes sk-fading-circle-delay {
  0%,
  39%,
  100% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
}

@keyframes sk-fading-circle-delay {
  0%,
  39%,
  100% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
}
</style>



