<template>
  <div id="cinema-manage">
    <!---->
    <div class="top">
      <el-col :span="12">
        <el-input placeholder="" v-model="input" class="input-with-select" style="width: 100%">
          <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
        </el-input>
      </el-col>
      <el-col :span="2" :offset="1">
        <el-button type="primary" size="small" @click="addCinema"></el-button>
      </el-col>
    </div>
    <!---->
    <div class="cinema-table">
      <el-table border :data="tableData">
        <el-table-column label=" ID" align="center" width="70" prop="id"> </el-table-column>
        <el-table-column label="" width="220" prop="nm"> </el-table-column>
        <el-table-column label="" align="center" width="120" prop="brandName"> </el-table-column>
        <el-table-column label="" show-overflow-tooltip prop="addr"> </el-table-column>
        <el-table-column label="" width="110" prop="endorse" :formatter="formatBoolean"> </el-table-column>
        <el-table-column label="" width="110" prop="allowrefund" :formatter="formatBoolean"> </el-table-column>
        <el-table-column width="200" align="center" label="">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!---->
    <div class="block" v-if="tableData.length">
      <el-pagination @current-change="currentChange" background layout="prev, pager, next" :page-size="8" :page-count="total"> </el-pagination>
    </div>
    <!---->
    <div>
      <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible" :modal-append-to-body="false" v-if="dialogFormVisible" :showClose="false">
        <el-form :label-position="labelPosition" label-width="80px" :model="cinemaInfo" :rules="rules">
          <el-form-item label="" prop="nm">
            <el-col :span="18">
              <el-input v-model="cinemaInfo.nm"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="brandId">
            <el-col :span="18">
              <el-select v-if="cinemaInfo.id" v-model="cinemaInfo.brandId" filterable placeholder="" style="width: 100%" @blur="selectBlur">
                <el-option v-for="(item, index) in brandList" :key="index" :label="item.brand" :value="item.id"></el-option>
              </el-select>
              <el-select v-else v-model="addBrandId" filterable placeholder="" style="width: 100%" @blur="selectBlur">
                <el-option v-for="(item, index) in brandList" :key="index" :label="item.brand" :value="item.id"></el-option>
              </el-select>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="addr">
            <el-col :span="18">
              <el-input v-model="cinemaInfo.addr" type="textarea"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="endorse">
            <el-col :span="16">
              <el-select v-model="cinemaInfo.endorse" placeholder="" style="width: 100%">
                <el-option label="" :value="true"></el-option>
                <el-option label="" :value="false"></el-option>
              </el-select>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="allowrefund">
            <el-col :span="16">
              <el-select v-model="cinemaInfo.allowrefund" placeholder="" style="width: 100%">
                <el-option label="" :value="true"></el-option>
                <el-option label="" :value="false"></el-option>
              </el-select>
            </el-col>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancel"> </el-button>
          <el-button type="primary" @click="manageCinemaInfo()"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { MessageBox, Message } from 'element-ui'
import { getCurrentPageCinema, updateCinemaInfo, deleteCinemaInfo, getBrandList, addCinemaBrand } from '@/api/index.js'
import Vue from 'vue'
// import chinaEcharts from '@/components/Home/CinemaManage/ChinaEchars/index.vue'
export default {
  name: 'CinemaManage',
  data() {
    let checkPhone = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''))
      } else {
        if (/^1[3|4|5|6|7|8][0-9]{9}$/.test(value)) {
          callback()
        } else {
          callback(new Error(''))
        }
      }
    }
    return {
      tableData: [],
      total: 0,
      currentPage: 1,
      cinemaInfo: {},
      brandList: [],
      dialogFormVisible: false,
      dialogTitle: '',
      newBrand: '',
      addBrandId: '',
      labelPosition: 'right',
      input: '',
      searchInput: '',
      booleanOpitons: [
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }
      ],
      // 
      rules: {
        cinema_name: [{ required: true, message: '', trigger: 'blur' }],
        cinema_phone: [{ required: true, message: '', trigger: 'blur' }, { validator: checkPhone, trigger: 'blur' }],
        specified_address: [{ required: true, message: '', trigger: 'blur' }]
      },
    }
  },
  components: {
    // chinaEcharts
  },
  created() {
    this.loadCurrentPageCinema(this.currentPage, 8, '')
  },
  methods: {
    // 
    async loadCurrentPageCinema(currentPage, pageSize, input) {
      let json = await getCurrentPageCinema(currentPage, pageSize, input)
      if (json.state === 200) {
        this.tableData = json.data.beanList
        this.total = json.data.tr
      }
    },
    // list
    async loadBrandList() {
      let json = await getBrandList()
      if (json.state === 200) {
        this.brandList = json.data
      }
    },
    formatBoolean: function(row, column, cellValue) {
      var ret = '' //
      if (cellValue) {
        ret = '' //
      } else {
        ret = ''
      }
      return ret
    },
    handleEdit(index, row) {
      this.loadBrandList()
      this.dialogTitle = ''
      this.cinemaInfo = row
      this.dialogFormVisible = true
      this.newBrand = ''
    },
    handleDelete(index, row) {
      MessageBox.confirm(', ', '').then(async value => {
        if (value === 'confirm') {
          let json = await deleteCinemaInfo(row.id)
          if (json.state === 200) {
            Message.success('')
            this.loadCurrentPageCinema(this.currentPage, 8, this.searchInput)
          }
        }
      })
    },
    selectBlur(e) {
      this.cinemaInfo.brandId = e.target.value
      this.newBrand = e.target.value
      this.addBrandId = e.target.value
    },
    //
    async manageCinemaInfo() {
      if (!this.cinemaInfo.nm || !this.cinemaInfo.addr) {
        Message.error('')
      } else {
        var reg = new RegExp('[\\u4E00-\\u9FFF]+', 'g')
        if (reg.test(this.newBrand)) {
          let json = await addCinemaBrand(this.newBrand)
          if (json.state === 200) {
            this.cinemaInfo.brandId = json.data
            this.newBrand = ''
            this.loadBrandList()
          } else {
            Message.error(json.message)
            return
          }
        }
        Vue.delete(this.cinemaInfo, 'brandName')
        if (this.dialogTitle === '') {
          let json = await updateCinemaInfo(this.cinemaInfo)
          if (json.state === 200) {
            this.loadCurrentPageCinema(this.currentPage, 8, this.searchInput)
            this.dialogFormVisible = false
            Message.success('')
          } else {
            Message.error(json.message)
          }
        } else {
          if (!this.cinemaInfo.brandId) {
            this.cinemaInfo.brandId = this.addBrandId
          }
          let json = await updateCinemaInfo(this.cinemaInfo)
          if (json.state === 200) {
            this.loadCurrentPageCinema(this.currentPage, 8, this.searchInput)
            this.dialogFormVisible = false
            this.addBrandId = ''
            Message.success('')
          } else {
            Message.error(json.message)
          }
        }
      }
    },
    async currentChange(currentPage) {
      this.currentPage = currentPage
      this.loadCurrentPageCinema(this.currentPage, 8, this.searchInput)
    },
    //
    search() {
      this.searchInput = this.input
      this.loadCurrentPageCinema(1, 8, this.searchInput)
    },
    //
    addCinema() {
      this.loadBrandList()
      this.dialogTitle = ''
      this.cinemaInfo = {}
      this.newBrand = ''
      this.dialogFormVisible = true
    },
    cancel() {
      this.loadCurrentPageCinema(this.currentPage, 8, this.searchInput)
      this.dialogFormVisible = false
    }
  }
}
</script>

<style>
.cinema-table {
  width: 90%;
  min-width: 900px;
  margin: 0 auto;
  margin-bottom: 30px;
}
.map {
  display: flex;
  justify-content: space-between;
}
.top {
  display: flex;
  justify-content: center;
  align-items: center;
  /* padding: 30px 0; */
  margin-bottom: 30px;
}
.block {
  text-align: center;
}
.el-dialog__header {
  text-align: center;
}
.el-dialog__body .el-form-item {
  width: 80%;
  padding-left: 20%;
}
</style>



